.button-wrapper {
    height: 4em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background: #e0e1e6;
    color: #888;
    -ms-flex: 1 1;
        flex: 1 1;
    outline: 1px solid #888;
    opacity: 0.8;
    -webkit-transition: opacity ease-in 0.2s;
    -o-transition: opacity ease-in 0.2s;
    transition: opacity ease-in 0.2s;
}

.button-wrapper:hover {
    opacity: 1;
    cursor: pointer;
}

.operator {
    background-color: #fe9241;
    color: white;
}



